
@font-face {
  font-family: KodomoRounded;
  font-style: Regular;
  src: url("/fonts/KodomoRounded.otf");
}
body {
  padding: 0;
  margin: 0;
  background-color: #b53543;
  font-family: "Nico Moji";
}
.main {
  min-height: calc(100vh - 60px);
}
#my-name {
  color: white;
  font-size: 30px;
  text-align: center;
  margin: 0;
  padding: 30px;
}
header {
  background-color: #f5475a;
  border-bottom: 3px solid white;
  border-top: 3px solid white;
}
footer {
  background-color: #361014;
  height: 60px;
}
#header-container {
  max-width: 460px;
  margin: 0 auto;
  padding: 10px 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: space-evenly;
      -ms-flex-pack: space-evenly;
          justify-content: space-evenly;
}
.icon {
  height: 50px;
  width: 50px;
  border-radius: 50%;
  border: 3px solid white;
  background-color: #56b584;
  -webkit-filter: drop-shadow(0px 2px 3px rgba(0, 0, 0, 0.6));
          filter: drop-shadow(0px 2px 3px rgba(0, 0, 0, 0.6));
}
#card-container {
  max-width: 460px;
  margin: 0 auto;
  padding: 20px 5px;
}
.card {
  background-color: #db4051;
  border: 3px solid white;
  border-radius: 3px;
  color: white;
  padding: 10px;
}
.card {
  animation: fadeIn 0.5s ease 0s 1 normal;
  -webkit-animation: fadeIn 0.5s ease 0s 1 normal;
}
@keyframes fadeIn {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@-webkit-keyframes fadeIn {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
.active {
  opacity: 0.5;
  animation: icon-fadeIn 0.5s ease 0s 1 normal;
  -webkit-animation: icon-fadeIn 0.5s ease 0s 1 normal;
}
@keyframes icon-fadeIn {
0% {
    opacity: 1;
}
100% {
    opacity: 0.5;
}
}
@-webkit-keyframes icon-fadeIn {
0% {
    opacity: 1;
}
100% {
    opacity: 0.5;
}
}
.card-title {
  margin-top: 0;
  font-size: 25px;
  margin-bottom: 10px;
  border-bottom: 3px solid white;
}
.card-content {
  font-size: 16px;
  line-height: 2;
  letter-spacing: 1.1px;
}

a[data-v-83f4ab64] {
  color: #b7e6ff;
}
section p[data-v-83f4ab64] {
  text-align: center;
}
.title[data-v-83f4ab64] {
  margin-top: 0;
  border-bottom: 3px solid white;
}
